<template>
    <section class="search">
        <h1>CSGO Stat Tracker</h1>
        <form v-on:submit.prevent="onSubmit">
            <div class="form-group">
                <label for="userId">View Player Stats</label>
                <input 
                    type="text" 
                    name="text" 
                    v-model="userId" 
                    id="userId"
                    placeholder="Steam username or ID"
                />
            </div>
            <div class="form-group">
                <input type="submit" value="Submit" class="btn"/>
            </div>
        </form>
    </section>
</template>

<script>
export default {
    name: 'Search',
    data() {
        return {
            userId: ""
        };
    },
    beforeCreate() {
        document.body.className = "body-bg-image";
    },
    methods: {
        onSubmit() {
            if (!this.userId) {
                this.$toasted.show("Enter a valid ID", {
                    duration: 3000, 
                    icon: "exclamation-circle"
                });
            } else {
                this.$router.push(`/profile/steam/${this.userId}`);
            }
        }
    }
};
</script>