<template>
  <section>
    <div v-if="loading">
      <h3>Loading...</h3>
    </div>

    <div v-if="error">
      <h1>{{error}}</h1>
      <p>Try entering a Steam ID if the error persists</p>
      <router-link to="/">Return to Search</router-link>
    </div>

    <div v-if="profileData" class="container">
      <h1 class="userId">{{profileData.platformInfo.platformUserHandle}}</h1>
      <div class="grid">
        <div>
          <img :src="profileData.platformInfo.avatarUrl" alt class="avatar" />
        </div>

        <div>
          <ul>
            <li>
              <h4>Time Played <i class="far fa-clock"></i></h4>
              <p>{{profileData.segments[0].stats.timePlayed.displayValue}}</p>
            </li>
            <li>
              <h4>Lifetime Kills</h4>
              <p>{{profileData.segments[0].stats.kills.displayValue}}</p>
            </li>
            <li>
              <h4>Lifetime Deaths</h4>
              <p>{{profileData.segments[0].stats.deaths.displayValue}}</p>
            </li>
            <li>
              <h4>KDR</h4>
              <p>{{profileData.segments[0].stats.kd.displayValue}}</p>
            </li>
            <li>
              <h4>Bombs Planted</h4>
              <p>{{profileData.segments[0].stats.bombsPlanted.displayValue}}</p>
            </li>
            <li>
              <h4>Bombs Defused</h4>
              <p>{{profileData.segments[0].stats.bombsDefused.displayValue}}</p>
            </li>
          </ul>
        </div>

        <div>
          <ul>
            <li>
              <h4>Total Damage <i class="fas fa-crosshairs"></i></h4>
              <p>{{profileData.segments[0].stats.damage.displayValue}}</p>
            </li>
            <li>
              <h4>Shots Fired</h4>
              <p>{{profileData.segments[0].stats.shotsFired.displayValue}}</p>
            </li>
            <li>
              <h4>Shots Hit</h4>
              <p>{{profileData.segments[0].stats.shotsHit.displayValue}}</p>
            </li>
            <li>
              <h4>Shots Accuracy</h4>
              <p>{{profileData.segments[0].stats.shotsAccuracy.displayValue}}</p>
            </li>
            <li>
              <h4>Headshots</h4>
              <p>{{profileData.segments[0].stats.headshots.displayValue}}</p>
            </li>
            <li>
              <h4>Headshot Percentage</h4>
              <p>{{profileData.segments[0].stats.headshotPct.displayValue}}</p>
            </li>
          </ul>
        </div>

        <div>
          <ul>
            <li>
              <h4>Matches Played <i class="fas fa-trophy"></i></h4>
              <p>{{profileData.segments[0].stats.matchesPlayed.displayValue}}</p>
            </li>
            <li>
              <h4>Wins</h4>
              <p>{{profileData.segments[0].stats.wins.displayValue}}</p>
            </li>
            <li>
              <h4>Losses</h4>
              <p>{{profileData.segments[0].stats.losses.displayValue}}</p>
            </li>
            <li>
              <h4>Win Percentange</h4>
              <p>{{profileData.segments[0].stats.wlPercentage.displayValue}}</p>
            </li>
            <li>
              <h4>MVP</h4>
              <p>{{profileData.segments[0].stats.mvp.displayValue}}</p>
            </li>
            <li>
              <h4>Money Earned</h4>
              <p>${{profileData.segments[0].stats.moneyEarned.displayValue}}</p>
            </li>
          </ul>
        </div>
      </div>
      <router-link to="/">Return to Search</router-link>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "Profile",
  data() {
    return {
      loading: false,
      error: null,
      profileData: null
    };
  },
  beforeCreate() {
    document.body.className = "body-bg-no-image";
  },
  async created() {
    this.loading = true;
    try {
      const res = await axios.get(
        `/api/v1/profile/steam/${this.$route.params.userId}`
      );

      this.profileData = res.data.data;
      console.log(this.profileData);
      this.loading = false;
    } catch (err) {
      this.loading = false;
      this.error = err.response.data.message;
    }
  }
};
</script>

<style scoped>
.container {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  max-width: 1600px;
  margin: 1rem auto;
  padding: 2rem 1.5rem;
  border-radius: 20px;
}

h1.userId {
  font-size: 2rem;
  background: rgba(0, 0, 0, 0.6);
  padding: 0.3rem 0.5rem;
  text-align: center;
  border-radius: 20px;
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
}

a {
  display: inline-block;
  margin-top: 2rem;
  border: #fff 2px solid;
  padding: 0.5rem 0.8rem;
}

a:hover {
  border: #ccc 2px solid;
  color: #ccc;
}

.platform-avatar {
  width: 40px;
  margin-right: 0.7rem;
}

img {
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}

li {
  background: rgba(0, 0, 0, 0.6);
  padding: 1rem;
  margin-bottom: 0.7rem;
  border-radius: 10px;
}

li p {
  font-size: 2rem;
}

li:first-child p {
  font-size: 1.5rem;
}

li span {
  font-size: 1rem;
  color: #ccc;
}

@media (max-width: 980px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 560px) {
  h1 {
    font-size: 1.5rem;
    display: block;
    text-align: center;
  }

  .grid {
    grid-template-columns: 1fr;
  }

  li p {
    font-size: 1.5rem;
  }
}
</style>